"use server";

import { inngest } from "@/inngest/client";

export async function triggerScan(scanId: string) {
    await inngest.send({
        name: "analysis.submitted",
        data: { scanId },
    });
}

export async function startPublicScan(data: { target_username: string; target_gender: "male" | "female" }) {
    try {
        const response = await fetch("http://127.0.0.1:8000/scan", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify(data),
        });

        if (!response.ok) {
            throw new Error(`Microservice error: ${response.statusText}`);
        }

        const result = await response.json();
        return { success: true, data: result };
    } catch (error) {
        console.error("Public scan failed:", error);
        return { success: false, error: "Failed to connect to scanner service" };
    }
}
