"use client";

import { motion } from "framer-motion";
import { Barcode, User } from "lucide-react";

interface TargetInfo {
    username: string;
    full_name: string;
    profile_pic: string;
}

interface ReceiptCardProps {
    score: number;
    redFlags: string[];
    targetInfo?: TargetInfo;
}

export function ReceiptCard({ score, redFlags, targetInfo }: ReceiptCardProps) {
    return (
        <motion.div
            initial={{ y: -100, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ type: "spring", stiffness: 100, damping: 20 }}
            className="relative w-full max-w-md mx-auto bg-white text-black font-mono p-6 shadow-2xl"
            style={{
                clipPath: "polygon(0 0, 100% 0, 100% 100%, 0 100%)", // Fallback
                maskImage: "radial-gradient(circle at bottom, transparent 6px, black 6px)",
                maskSize: "20px 20px",
                maskPosition: "bottom",
                maskRepeat: "repeat-x",
                paddingBottom: "2rem",
            }}
        >
            {/* Jagged Edge Top */}
            <div
                className="absolute top-0 left-0 w-full h-4 bg-white"
                style={{
                    maskImage: "linear-gradient(45deg, transparent 50%, black 50%), linear-gradient(-45deg, transparent 50%, black 50%)",
                    maskSize: "20px 20px",
                    maskRepeat: "repeat-x",
                    maskPosition: "0 0",
                    top: "-10px"
                }}
            />

            <div className="text-center border-b-2 border-dashed border-black pb-4 mb-4 relative">
                {targetInfo && (
                    <div className="flex justify-center mb-4 relative">
                        <div className="relative">
                            <div className="w-24 h-24 rounded-full border-4 border-red-600 overflow-hidden grayscale contrast-125">
                                {targetInfo.profile_pic ? (
                                    <img
                                        src={targetInfo.profile_pic}
                                        alt="Target"
                                        className="w-full h-full object-cover"
                                        onError={(e) => {
                                            (e.target as HTMLImageElement).style.display = 'none';
                                            (e.target as HTMLImageElement).nextElementSibling?.classList.remove('hidden');
                                        }}
                                    />
                                ) : null}
                                <div className={`w-full h-full bg-gray-200 flex items-center justify-center ${targetInfo.profile_pic ? 'hidden' : ''}`}>
                                    <User className="w-12 h-12 text-gray-400" />
                                </div>
                            </div>
                            <div className="absolute -bottom-2 -right-2 bg-black text-white text-[10px] font-bold px-2 py-1 rotate-[-10deg] border border-white">
                                TARGET
                            </div>
                        </div>
                    </div>
                )}

                <h2 className="text-2xl font-bold tracking-tighter">CAUGHT IN 4K</h2>
                <p className="text-sm uppercase">AI Loyalty Audit</p>
                {targetInfo && (
                    <p className="text-xs font-bold mt-1 uppercase">@{targetInfo.username}</p>
                )}
                <p className="text-xs mt-1 text-gray-500">{new Date().toLocaleString()}</p>
            </div>

            <div className="space-y-4 mb-6">
                <div className="flex justify-between items-end">
                    <span className="text-sm uppercase">Loyalty Score</span>
                    <span className="text-4xl font-bold">{score}%</span>
                </div>

                <div className="border-t-2 border-dashed border-black pt-4">
                    <h3 className="text-sm font-bold uppercase mb-2">Red Flags Found:</h3>
                    <ul className="space-y-2 text-sm">
                        {redFlags.map((flag, index) => (
                            <li key={index} className="flex justify-between">
                                <span>- {flag}</span>
                                <span className="font-bold">DETECTED</span>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>

            <div className="text-center border-t-2 border-dashed border-black pt-4">
                <p className="text-xs uppercase mb-2">Verdict: {score < 50 ? "TOXIC" : "SAFE"}</p>
                <div className="flex justify-center">
                    <Barcode className="w-full h-12 opacity-80" />
                </div>
                <p className="text-[10px] mt-2 uppercase">No Refunds on Truth</p>
            </div>

            <div className="absolute bottom-0 left-0 w-full h-4 bg-white"
                style={{
                    maskImage: "linear-gradient(45deg, black 50%, transparent 50%), linear-gradient(-45deg, black 50%, transparent 50%)",
                    maskSize: "20px 20px",
                    maskRepeat: "repeat-x",
                    maskPosition: "0 100%",
                    bottom: "-10px"
                }}
            />
        </motion.div>
    );
}
